<template>
    <div>
        <h1>글을 작성하는 공간입니다</h1>

        <input type="text" v-model="title"> <br>
        <textarea v-model="memo" cols="30" rows="10"></textarea> <br>
        <button @click="addmemoMutation">완료</button>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            title: '',
            memo: '',
        }
    },
    methods: {
        addmemo: function() {
            const id = this.$store.state.board.length + 1;
            this.$store.state.board.push({ id: id, 
                title: this.title, memo: this.memo })
        },
        addmemoMutation: function() {
            this.$store.commit('addmemo', { title: this.title, memo: this.memo} );
            this.$router.push('/');
        }
    }
}
</script>